import React from "react";
import PortfolioCaption from "./PortfolioCaption";
import PortfolioImage from "./PortfolioImage";

export default function PortfolioLink(props) {
    return (
        <a className="portfolio-link" href={props.liveLink}>
            <PortfolioCaption />
            <PortfolioImage
                imgSrc={props.imgSrc}
            />
        </a>
    );
}